<template>
    <div class="wrap">
        <!--查询表单-->
        <el-form class="query-form" label-position="left" size="small" :inline="true" :model="formQuery" label-width='70px'>
            <el-form-item label="合同名称">
                <el-input v-model="formQuery.clue_name" clearable placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submit">查询</el-button>
            </el-form-item>
            <el-form-item>
                &nbsp;&nbsp;&nbsp;&nbsp;<a class="text-base" @click='query_show = !query_show; '>更多查询条件<i :class="'iconfont icon-'+query_show"></i></a>
            </el-form-item>
            <div v-show="query_show">
                <div class="input-box">
                    <el-form-item label="合同状态">
                        <el-select v-model="formQuery.follow_type" clearable>
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="采购商">
                        <el-input v-model="formQuery.clue_owner" clearable placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="跟进状态">
                        <el-select v-model="formQuery.follow_type" clearable>
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
                <div class="input-box">
                    <el-form-item label="审批状态">
                        <el-select v-model="formQuery.follow_type" clearable>
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="跟进时间">
                        <el-date-picker 
                            v-model="formQuery.recent_follow_time"
                            type="daterange"
                            style="width: 250px; margin-right: 30px;"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            value-format="yyyy-MM-dd"
                            :default-time="['00:00:00', '23:59:59']">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="项目">
                        <el-select v-model="formQuery.classify" clearable>
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
            </div>
        </el-form>
        <!-- 操作按钮 -->
        <el-row>
            <el-button type="primary" size="small" @click="dialogAddVisibl">新增</el-button>
            <el-button type="primary" size="small" @click="dialogTransferVisible = true">转给他人</el-button>
            <el-button type="primary" size="small" @click="handleSubmit">提交审批</el-button>
            <el-button type="primary" size="small" @click="dialogImportVisible = true">导入</el-button>
            <el-button type="primary" size="small" @click="handleDel">删除</el-button>
            <el-button type="primary" size="small">导出合同</el-button>
        </el-row>
        <!--客户表格数据展示-->
        <keep-alive>
        <el-table class="table"  :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                max-height="500" border style="width: 100%; margin: 20px 0 0;"
                header-cell-class-name='text-center'
                cell-class-name='text-center' :fit='true'
                @selection-change="handleSelectionChange"
                :default-sort="{prop: '', order: 'descending'}">
            <el-table-column :reserve-selection='true' type="selection" width="40"></el-table-column>
            <el-table-column prop="company_name" label="合同编号"></el-table-column>
            <el-table-column prop="filed_must_own" sortable label="合同名称"></el-table-column>
            <el-table-column prop="filed_must_cus_type" min-width="100" sortable label="采购商"></el-table-column>
            <el-table-column prop="filed_must_cus_size" label="合同总金额"></el-table-column>
            <el-table-column prop="filed_must_industry" label="项目"></el-table-column>
            <el-table-column prop="filed_must_phone" label="合同开始时间"></el-table-column>
            <el-table-column prop="filed_must_state" label="合同状态"></el-table-column>
            <el-table-column prop="filed_must_followTime" label="创建人"></el-table-column>
            <el-table-column prop="filed_must_followTime" label="创建时间"></el-table-column>
            <el-table-column prop="filed_must_followTime" label="审批状态"></el-table-column>
        </el-table>
        </keep-alive>
        <!--分页-->
        <el-pagination v-if="tableData.length" background layout="total,prev, pager, next"
            :page-size="pagesize"
            @current-change="currentPage = val"
            :current-page="currentPage"
            :total="tableData.length" style="float: right;">
        </el-pagination>
        <!-- 新增合同模态框 -->
        <el-dialog title="新增合同" width="600px" class="dialogAdd" :visible.sync="dialogAddVisible" :close-on-click-modal='false'>
            <el-form ref="form" :model="formAdd" label-width="120px" size="small">
                <el-form-item label="合同编号：">
                    <el-input disabled v-model="formAdd.name"></el-input>
                </el-form-item>
                <el-form-item label="合同名称：">
                    <el-input v-model="formAdd.name"></el-input>
                </el-form-item>
                <el-form-item label="采购商：">
                    <el-input v-model="formAdd.name"></el-input>
                </el-form-item>
                <el-form-item label="合同总金额(元)：">
                    <el-input v-model="formAdd.name"></el-input>
                </el-form-item>
                <el-form-item label="项目：">
                    <el-select v-model="formAdd.origin" placeholder="请选择">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="开始日期：">
                    <el-date-picker
                    v-model="formAdd.value1"
                    type="date"
                    placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="结束日期：">
                    <el-date-picker
                    v-model="formAdd.value1"
                    type="date"
                    placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="合同负责人：">
                    <el-select v-model="formAdd.origin" placeholder="默认自己">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="所属部门：">
                    <el-select v-model="formAdd.origin" placeholder="请选择部门">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="合同状态：">
                    <el-select v-model="formAdd.origin" placeholder="请选择">
                    <el-option label="区域一" value="shanghai"></el-option>
                    <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="客户签约人：">
                    <el-input v-model="formAdd.notes"></el-input>
                </el-form-item>
                <el-form-item label="我方签约人：">
                    <el-input v-model="formAdd.notes"></el-input>
                </el-form-item>
                <el-form-item label="签约日期：">
                    <el-date-picker
                    v-model="formAdd.value1"
                    type="date"
                    placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="备注：">
                    <el-input type="textarea" v-model="formAdd.notes" :rows="4"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogAddVisible = false">确 定</el-button>
            </div>
        </el-dialog>
        <!-- 转给他人模态框 -->
        <el-dialog title="转给他人" width="600px" class="dialogTransfer" :visible.sync="dialogTransferVisible" :close-on-click-modal='false'>
            <el-form ref="form" :model="formTransfer" label-width="120px" size="small">
                <el-form-item label="转移对象：">
                    机会名称
                </el-form-item>
                <el-form-item label="新拥有者：">
                    <el-select v-model="formTransfer.origin">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogTransferVisible = false" size="small">取消</el-button>
                <el-button type="primary" @click="dialogTransferVisible = false" size="small">确定</el-button>
            </div>
        </el-dialog>
        <!--导入客户弹框-->
        <el-dialog title="导入客户" class="dialogImport" width="40%" :visible.sync="dialogImportVisible" :close-on-click-modal='false'>
            <p style="margin-top: 30px;">1.将要导入的数据填写到模板文件中：</p>
            <p><a href="" class="download">点击下载导入模板</a></p>
            <p>注意：模板表头不能更改，也不能删除；客户名称、拥有者为必填，必须保留；单次数据不能超过500条。</p>
            <p style="margin-top: 20px;">2.请选择数据重复的操作方式：</p>
            <el-select v-model="formImport.type" placeholder="" size="small">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
            </el-select>
            <p>查重规则：客户名称</p>
            <p style="margin-top: 20px;">3.选择要导入的数据文件</p>
            <el-upload
                class="upload-demo"
                ref="upload"
                action=""
                :file-list="formImport.fileList"
                :auto-upload="false">
                <el-button slot="trigger" size="small" type="primary">上传文件</el-button>
            </el-upload>
            <div slot="footer" class="dialog-footer">
                <a href="" class="import-history">查看历史导入记录</a>
                <el-button @click="dialogImportVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogImportVisible = false">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import { mapState, mapActions, mapMutations } from 'vuex'
    export default {
        data() {
            return {
                query_show: false,          //  是否显示更多查询
                currentPage: 1,//默认开始页面
                pagesize: 2,//每页的数据条数
                formQuery: {
                    clue_name: '',
                    clue_owner: '',
                    company_name: '',
                    follow_type: '',
                    phone: '',
                    recent_follow_time: [],
                    classify: ''
                },
                tableData: [{company_name: '3412'},{company_name: '3412sddfdsg'}],
                multipleSelection: [],  //  表格选中数据
                formAdd: {},     //  新增机会表单
                formTransfer: {},     //  转给他人表单
                formImport: {},     //  导入表单
                dialogAddVisible: false,  // 新增
                dialogImportVisible: false,  // 导入
                dialogDelVisible: false,  // 删除
                dialogTransferVisible: false,  // 转给他人
            }
        },
        methods: {
            dialogAddVisibl(){
                console.log(this.multipleSelection)
                this.dialogAddVisible = true
                console.log(this.multipleSelection)
            },
            /**
             * 搜索查询
             */
            submit(){
                console.log(this.formQuery);
            },
            
            /**
             * 表格勾选
             */
            handleSelectionChange(val){
                this.multipleSelection = val
            },
            /**
             * 删除操作
             */
            handleDel(){
                const h = this.$createElement;
                this.$msgbox({
                    title: '提示',
                    message: "确定要删除【xxxx】吗？",
                    showCancelButton: true,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(action => {
                    console.log(2)
                }).catch(()=>{});
            },
            /**
             * 提交审批操作
             */
            handleSubmit(){
                const h = this.$createElement;
                this.$msgbox({
                    title: '提示',
                    message: "确定提交审批吗？",
                    showCancelButton: true,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(action => {
                    console.log(2)
                }).catch(()=>{});
            },
        }
    }
</script>
<style lang="less" scoped>
    .query-form{
        .el-input, .el-select, .el-date-picker{
            width: 250px;
            margin-right: 30px;
        }
    }
    .dialogAdd, .dialogTransfer{
        .el-input, .el-select, .el-textarea{
            width: 330px;
        }
    }
    .dialogImport {
        p {
            margin-bottom: 5px;
        }
        .dialog-footer {
            margin-top: 30px;
        }
        .download {
            color: #0090ff;
            text-decoration: underline;
        }
        .import-history {
            float: left;
            line-height: 40px;
            font-size: 15px;
            color: #0090ff;
        }
    }
</style>




